

.accordion {

  width: 100%;
  margin-top: 16px; 
  border: 2px solid #ccc; 
  border-radius: 10px; 
}


.accordion-summary {
  display: flex;
  align-items: center;
  padding: 8px 16px;
  cursor: pointer;
  background-color: #f0f0f0; 
}


.accordion-details {
  display: flex;
  flex-direction: column;
  padding: 16px;
}

.details-row {
  display: flex;
  align-items: center;
  margin-bottom: 12px; 
}


.detail-label {
  margin-right: 8px; 
  font-weight: bold;
}

.detail-value {
  font-weight: normal;
}

.description-textarea {
  width: 100%;
  margin-bottom: 12px;
  padding: 8px;
  border: 1px solid #ccc; 
  border-radius: 5px; 
  font-size: 14px;
}


.actions-row {
  display: flex;
  justify-content: flex-end;
  margin-top: 12px; 
}


.action-button {
  margin-left: 8px; 
}
